'use strict';

/**
* Allocate a buffer from an `ArrayBuffer`.
*
* @module @stdlib/buffer/from-arraybuffer
*
* @example
* import ArrayBuffer from '@stdlib/array/buffer' ;
* import arraybuffer2buffer from '@stdlib/buffer/from-arraybuffer' ;
*
* var ab = new ArrayBuffer( 10 );
* var buf = arraybuffer2buffer( ab );
* // returns <Buffer>
*/

// MODULES //

import hasFrom from './has_from.js' ;
import main from './main.js' ;
import polyfill from './polyfill.js' ;


// MAIN //

var arraybuffer2buffer;
if ( hasFrom ) {
	arraybuffer2buffer = main;
} else {
	arraybuffer2buffer = polyfill;
}


// EXPORTS //

export default arraybuffer2buffer;
