'use strict';

/**
* Test if a value is object-like.
*
* @module @stdlib/assert/is-object-like
*
* @example
* import isObjectLike from '@stdlib/assert/is-object-like' ;
*
* var bool = isObjectLike( {} );
* // returns true
*
* bool = isObjectLike( [] );
* // returns true
*
* bool = isObjectLike( null );
* // returns false
*
* @example
* import { isObjectLikeArray as isObjectLike } from '@stdlib/assert/is-object-like' ;
*
* var bool = isObjectLike( [ {}, [] ] );
* // returns true
*
* bool = isObjectLike( [ {}, '3.0' ] );
* // returns false
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property' ;
import arrayfun from './../../tools/array-function' ;
import main from './main.js' ;


// VARIABLES //

var isObjectLikeArray = arrayfun( main );


// MAIN //

setReadOnly( main, 'isObjectLikeArray', isObjectLikeArray );


// EXPORTS //

export default main;
