'use strict';

// MODULES //

import hasArgumentsBug from './has_arguments_bug.js' ;
import HAS_BUILTIN from './has_builtin.js' ;
import builtin from './builtin.js' ;
import wrapper from './builtin_wrapper.js' ;
import polyfill from './polyfill.js' ;


// MAIN //

/**
* Returns an array of an object's own enumerable property names.
*
* @name keys
* @type {Function}
* @param {*} value - input object
* @returns {Array} a list of own enumerable property names
*
* @example
* var obj = {
*     'beep': 'boop',
*     'foo': 3.14
* };
*
* var k = keys( obj );
* // e.g., returns [ 'beep', 'foo' ]
*/
var keys;
if ( HAS_BUILTIN ) {
	if ( hasArgumentsBug() ) {
		keys = wrapper;
	} else {
		keys = builtin;
	}
} else {
	keys = polyfill;
}


// EXPORTS //

export default keys;
