'use strict';

// MODULES //

import isFunction from '@stdlib/assert/is-function' ;
import builtin from './native.js' ;
import polyfill from './polyfill.js' ;


// MAIN //

var getProto;
if ( isFunction( Object.getPrototypeOf ) ) {
	getProto = builtin;
} else {
	getProto = polyfill;
}


// EXPORTS //

export default getProto;
